/* 헤더 */
.header {
  position: fixed;
  top: 0;
  display: flex;
  width: 100%;
  height: 50px;
  background: rgb(186,197,207);
  align-items: center;
  justify-content: center;
  color: #fff;
  z-index: 9999;
  
  .logo {
    position: absolute;
    top: 5px;
    left: 20px;
    width: 40px;
    height : 40px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-radius: 10px;
    transition: all .3s ease;
  }

  .logo:hover {
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.22) 0px -12px 30px, rgba(0, 0, 0, 0.22) 0px 4px 6px, rgba(0, 0, 0, 0.27) 0px 12px 13px, rgba(0, 0, 0, 0.19) 0px -3px 5px;
  }

  li {
    position: relative;
    display: inline-block;
    padding: 5px 10px;
  }

  li:before {
    display: none;
    position: absolute;
    top: 30px;
    left: 0;
    content: '';
    width: 100%;
    height: 3px;
    background-color: rgb(211,219,226);
    transition: all .3s ease-in-out;
  }

  li.active:before {
    display: block;
  }

  a {
    font-weight: 700;
  }
}